<template>
    <div class="appbox">
        <form action>
            <label for="problem">问题和意见</label>
            <textarea v-model="opinionForm.content" id="problem" name="" placeholder="具体说说您的问题，描述不少于20个字，我们会尽快为您解决。"></textarea>
            <label for="contact">您的联系方式</label>
            <input v-model="opinionForm.email" id="contact" type="email" name="" placeholder="邮箱" />
        </form>
        <button @click="submitOpinion" class="verify">提交</button>
    </div>
</template>

<script>
    import { opinion } from "@/api/user"
    import Toast from 'vant/lib/toast'
    import 'vant/lib/toast/style'
    import Dialog from 'vant/lib/dialog'
    import 'vant/lib/dialog/style'
    import { isEmail } from "vant/es/utils/validate/email";

    export default {
        name: "Opinion",
        data()
        {
            return {
                opinionForm:{
                    content:'',
                    email:''
                }
            }
        },
        methods:{

            submitOpinion()
            {

                if (this.opinionForm.content == '' || this.opinionForm.content.length < 20)
                {
                    Toast('描述不少于20个字')
                    return
                }
                if (this.opinionForm.content.length > 200)
                {
                    Toast('描述不超过200个字')
                    return
                }
                if (this.opinionForm.email == '')
                {
                    Toast('请填写邮箱')
                    return
                }
                if (!isEmail(this.opinionForm.email))
                {
                    Toast('请填写正确邮箱')
                    return
                }

                opinion(this.opinionForm).then(res => {

                    Dialog.alert({
                        message: '已成功提交, 处理后会尽快回复您!'
                    }).then(() => {

                        this.$router.replace('/mine')
                    });
                })
            }
        }
    }
</script>

<style scoped>
    @import "../../static/css/opinion.css";
</style>



